'use client';

import React from 'react';
import { useCallback, useMemo, useTransition } from 'react';
import { useRouter } from 'next/navigation';
import dynamic from 'next/dynamic';
import {
  Box,
  Button,
  TextField,
  Typography,
  Paper,
  InputAdornment,
  IconButton,
  CircularProgress,
  Alert,
  Fade,
  useTheme,
  useMediaQuery,
} from '@mui/material';
import {
  LockOutlined,
  EmailOutlined,
  Visibility,
  VisibilityOff,
  AdminPanelSettings,
  Person,
} from '@mui/icons-material';
import Link from 'next/link';
import { 
  buttonStyles, 
  paperStyles, 
  headerStyles, 
  iconContainerStyles, 
  iconStyles, 
  textFieldStyles,
  linkStyles
} from '../StyledComp';

// Dynamically import heavy components
const AdminIcon = dynamic(() => Promise.resolve(AdminPanelSettings));
const UserIcon = dynamic(() => Promise.resolve(Person));

const initialState = {
  email: '',
  password: '',
  showPassword: false,
  isLoading: false,
  error: '',
};

const LoginForm = ({ isAdmin = false }) => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
  const router = useRouter();
  const [isPending, startTransition] = useTransition();
  const [state, setState] = React.useState(initialState);

  const { email, password, showPassword, isLoading, error } = state;

  const handleChange = useCallback((e) => {
    const { name, value } = e.target;
    setState(prev => ({ ...prev, [name]: value }));
  }, []);

  const togglePasswordVisibility = useCallback(() => {
    setState(prev => ({ ...prev, showPassword: !prev.showPassword }));
  }, []);

  const handleSubmit = useCallback(async (e) => {
    e.preventDefault();
    
    if (!email || !password) {
      setState(prev => ({ ...prev, error: 'Please fill in all fields' }));
      return;
    }

    setState(prev => ({ ...prev, isLoading: true, error: '' }));

    try {
      // Simulate API call
      await new Promise(resolve => setTimeout(resolve, 1500));
      
      // Use startTransition for non-urgent updates
      startTransition(() => {
        router.push(isAdmin ? '/admin/dashboard' : '/dashboard');
      });
    } catch (err) {
      setState(prev => ({ ...prev, error: 'Invalid credentials. Please try again.' }));
    } finally {
      setState(prev => ({ ...prev, isLoading: false }));
    }
  }, [email, password, isAdmin, router]);

  const formProps = useMemo(() => ({
    email: {
      name: 'email',
      label: 'Email Address',
      type: 'email',
      autoComplete: 'email',
      startAdornment: <EmailOutlined color="action" />,
    },
    password: {
      name: 'password',
      label: 'Password',
      type: showPassword ? 'text' : 'password',
      autoComplete: 'current-password',
      startAdornment: <LockOutlined color="action" />,
      endAdornment: (
        <IconButton
          onClick={togglePasswordVisibility}
          edge="end"
          size="small"
          sx={{ color: 'text.secondary' }}
        >
          {showPassword ? <VisibilityOff /> : <Visibility />}
        </IconButton>
      ),
    },
  }), [showPassword, togglePasswordVisibility]);

  const buttonText = useMemo(() => 
    isLoading ? <CircularProgress size={24} color="inherit" /> 
    : `Sign In ${isAdmin ? 'as Admin' : ''}`.trim(),
    [isLoading, isAdmin]
  );

  const switchLoginText = useMemo(() => (
    isAdmin ? (
      <>
        User?{' '}
        <Typography
          component={Link}
          href="/login"
          color="primary"
          sx={linkStyles}
        >
          Go to User Login
        </Typography>
      </>
    ) : (
      <>
        Admin?{' '}
        <Typography
          component={Link}
          href="/admin/login"
          color="primary"
          sx={linkStyles}
        >
          Admin Login
        </Typography>
      </>
    )
  ), [isAdmin]);



  return (
    <Fade in={true} timeout={500}>
      <Paper
        elevation={6}
        sx={paperStyles}
      >
        <Box sx={headerStyles}>
          <Box sx={iconContainerStyles}>
            {isAdmin ? (
              <AdminIcon sx={iconStyles} />
            ) : (
              <UserIcon sx={iconStyles} />
            )}
          </Box>
          <Typography component="h1" variant="h5" fontWeight="bold">
            {isAdmin ? 'Admin Portal' : 'Welcome Back'}
          </Typography>
          <Typography variant="body2" color="text.secondary" mt={1}>
            {isAdmin ? 'Sign in to access the admin dashboard' : 'Sign in to continue'}
          </Typography>
        </Box>

        {error && (
          <Alert severity="error" sx={{ mb: 3, borderRadius: 1 }}>
            {error}
          </Alert>
        )}

        <Box component="form" onSubmit={handleSubmit} noValidate>
          {Object.entries(formProps).map(([key, props]) => (
            <TextField
              key={key}
              margin="normal"
              required
              fullWidth
              id={key}
              value={state[key]}
              onChange={handleChange}
              disabled={isLoading}
              InputProps={{
                startAdornment: (
                  <InputAdornment position="start">
                    {props.startAdornment}
                  </InputAdornment>
                ),
                ...(props.endAdornment && { endAdornment: props.endAdornment }),
              }}
              sx={textFieldStyles}
              {...props}
            />
          ))}

          <Button
            type="submit"
            fullWidth
            variant="contained"
            disabled={isLoading}
            size="large"
            sx={buttonStyles}
          >
            {buttonText}
          </Button>

          <Box sx={{ mt: 3, textAlign: 'center' }}>
            <Typography variant="body2" color="text.secondary">
              {switchLoginText}
            </Typography>
          </Box>
        </Box>
      </Paper>
    </Fade>
  );
};



export default LoginForm;